<template>
	<view :class="disabled ? 'uni-list-item--disabled' : ''" :hover-start-time="0" :hover-stay-time="0" :hover-class="disabled || showSwitch ? '' : 'uni-list-item--hover'" class="uni-list-item" @click.stop="onClick">
		<view class="uni-list-item__container">
			<view v-if="thumb" class="uni-list-item__icon">
				<image :src="thumb" class="uni-list-item__icon-img" />
			</view>
			<view v-else-if="showExtraIcon" class="uni-list-item__icon">
				<uni-icon class="uni-icon-wrapper" :color="extraIcon.color" :size="extraIcon.size" :type="extraIcon.type" />
			</view>
			<view class="uni-list-item__content">
				<view class="iconfont" style="margin-right: 20rpx;" v-if="leftIcon" :class="leftIcon" :style="{'color':iconColor,'font-size':leftIconSize}"></view>
				<view class="uni-list-item__content-title" :class="titleColor">{{ title }}</view>
				<view v-if="note" class="uni-list-item__content-note">{{ note }}</view>
				<slot></slot>
			</view>
			<view v-if="showBadge || showArrow || showSwitch" class="uni-list-item__extra" :style="'width:'+extraWidth+';'">
				<uni-badge v-if="showBadge" :type="badgeType" :text="badgeText" />
				<switch v-if="showSwitch" :disabled="disabled" :checked="switchChecked" @change="onSwitchChange" />
				<view class="iconfont" v-if="rightIcon" :class="rightIcon" :style="{'color': rightIconColor}"></view>
				<view v-if="rightText" :class="rightTextColor">{{rightText}}</view>
				<template v-if="showArrow">
					<slot name="icon"></slot>
				</template>
			</view>
		</view>
	</view>
</template>

<script>
	import uniIcon from '../uni-icon/uni-icon.vue'
	import uniBadge from '../uni-badge/uni-badge.vue'
	export default {
		name: 'UniListItem',
		components: {
			uniIcon,
			uniBadge
		},
		props: {
			rightTextColor:{
				type:String,
				default:''
			},
			rightText:{},
			titleColor:{
				type:String,
				default:''
			},
			rightIconColor:{
				type:String,
				default:'#000000'
			},
			iconColor:{
				type:String,
				default:'#000000'
			},
			rightIcon:{
				type:String,
				default:''
			},
			leftIcon:{
				type:String,
				default:''
			},
			leftIconSize:{
				type:String,
				default:''
			},
			title: {
				type: String,
				default: ''
			}, // 列表标题
			note: {
				type: String,
				default: ''
			}, // 列表描述
			disabled: { // 是否禁用
				type: Boolean,
				default: false
			},
			showArrow: { // 是否显示箭头
				type: Boolean,
				default: true
			},
			showBadge: { // 是否显示数字角标
				type: Boolean,
				default: false
			},
			showSwitch: { // 是否显示Switch
				type: Boolean,
				default: false
			},
			switchChecked: { // Switch是否被选中
				type: Boolean,
				default: false
			},
			badgeText: {
				type: [String, Number],
				default: ''
			}, // badge内容
			badgeType: { // badge类型
				type: String,
				default: 'success'
			},
			thumb: {
				type: String,
				default: ''
			}, // 缩略图
			showExtraIcon: { // 是否显示扩展图标
				type: Boolean,
				default: false
			},
			extraWidth:{
				type:String,
				default:''
			},
			extraIcon: {
				type: Object,
				default () {
					return {
						type: 'contact',
						color: '#000000',
						size: 20
					}
				}
			}
		},
		data() {
			return {

			}
		},
		methods: {
			onClick() {
				this.$emit('click',this.title)
			},
			onSwitchChange(e) {
				this.$emit('switchChange', e.detail)
			}
		}
	}
</script>

<style>
	@charset "UTF-8";

	.uni-list-item {
		font-size: 32rpx;
		position: relative;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		align-items: center
	}

	.uni-list-item--disabled {
		opacity: .3
	}

	.uni-list-item--hover {
		background-color: #e3e3e3;
	}

	.uni-list-item__container {
		padding: 24rpx 30rpx;
		width: 100%;
		box-sizing: border-box;
		flex: 1;
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center
	}

	.uni-list-item__container:after {
		position: absolute;
		z-index: 3;
		right: 0;
		bottom: 0;
		left: 0;
		height: 1px;
		content: '';
		-webkit-transform: scaleY(.5);
		transform: scaleY(.5);
		background-color: #EEEEEE
	}

	.uni-list-item__content {
		flex: 1;
		overflow: hidden;
		display: flex;
		align-items: center;
		flex-direction: row
		
		/* flex: 1;
		padding: 0;
		overflow: hidden;
		display: flex;
		flex-direction: row */
	}

	.uni-list-item__content-title {
		margin-right: 20rpx;
		font-size: 32rpx;
		text-overflow: ellipsis;
		white-space: nowrap;
		color: inherit;
		line-height: 1.5;
		overflow: hidden
	}

	.uni-list-item__content-note {
		margin-left: 20rpx;
		color: #999;
		font-size: 33rpx;
		white-space: normal;
		display: -webkit-box;
		-webkit-box-orient: vertical;
		-webkit-line-clamp: 2;
		overflow: hidden
	}

	.uni-list-item__extra {
		width: 25%;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		align-items: center
	}

	.uni-list-item__icon {
		margin-right: 18rpx;
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center
	}

	.uni-list-item__icon-img {
		height: 52rpx;
		width: 52rpx
	}

	.uni-list>.uni-list-item:last-child .uni-list-item-container:after {
		height: 0
	}
</style>